<script>
    import $ from 'jquery'
    import Files from "@/components/Files.vue"
    import CustomSelect from "@/components/CustomSelect.vue"
    export default {
        name: 'EditProgrammationsrondes',
        components: { CustomSelect,Files },
        props: ['data','gridApi','modalFormId',
                            'postesData',
                            'usersData',
                            'zonesData',
                    ],
        data () {
            return {
                errors: [],
                isLoading: false,
                form: {
                    
                        id:"",
                    
                        libelle:"",
                    
                        description:"",
                    
                        date_debut:"",
                    
                        date_fin:"",
                    
                        default_heure_debut:"",
                    
                        default_heure_fin:"",
                    
                        user_id:"",
                    
                        statut:"",
                    
                        type:"",
                    
                        postesbaladeur:"",
                    
                        valider1:"",
                    
                        zone_id:"",
                    
                        valider2:"",
                    
                        poste_id:"",
                    
                        etats:"",
                    
                        postes:"",
                    
                        min_pointage:"",
                    
                        Allclients:"",
                    
                        AllDatesInRange:"",
                    
                        Presents:"",
                    
                        Abscents:"",
                    
                        Presentsid:"",
                    
                        Abscentsid:"",
                    
                        user_id_2:"",
                    
                        user_id_3:"",
                    
                        user_id_4:"",
                    
                        valideur_1:"",
                    
                        valideur_2:"",
                    
                        creat_by:"",
                    
                        extra_attributes:"",
                    
                        created_at:"",
                    
                        updated_at:"",
                    
                        deleted_at:"",
                                    }
            }
        },

        mounted () {
            this.form = this.data
                                                                                                                                this.form['date_debut'] = this.form['date_debut'].split(' ')[0]
                                                          this.form['date_fin'] = this.form['date_fin'].split(' ')[0]
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              },
        methods: {

            EditLine () {
                this.isLoading = true
                this.axios.post('/api/programmationsrondes/' + this.form.id + '/update', this.form).then(response => {
                    this.isLoading = false
                    this.gridApi.applyServerSideTransaction({
                        update: [
                            response.data
                        ],
                    });
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            DeleteLine () {
                this.isLoading = true
                this.axios.post('/api/programmationsrondes/' + this.form.id + '/delete').then(response => {
                    this.isLoading = false

                    this.gridApi.applyServerSideTransaction({
                        remove: [
                            this.form
                        ]
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    console.log(error.response.data)
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de la suppression')
                })
            },
        }
    }
</script>
