<script>
    import $ from 'jquery'
    import Files from "@/components/Files.vue"
    import CustomSelect from "@/components/CustomSelect.vue"
    export default {
        name: 'CreateProgrammationsrondes',
        components: { CustomSelect,Files },
        props: [
            'gridApi',
            'modalFormId',
        'postesData',
        'usersData',
        'zonesData',
            ],
        data () {
            return {
                errors: [],
                isLoading: false,
                form: {
                
                id:"",
                
                libelle:"",
                
                description:"",
                
                date_debut:"",
                
                date_fin:"",
                
                default_heure_debut:"",
                
                default_heure_fin:"",
                
                user_id:"",
                
                statut:"",
                
                type:"",
                
                postesbaladeur:"",
                
                valider1:"",
                
                zone_id:"",
                
                valider2:"",
                
                poste_id:"",
                
                etats:"",
                
                postes:"",
                
                min_pointage:"",
                
                Allclients:"",
                
                AllDatesInRange:"",
                
                Presents:"",
                
                Abscents:"",
                
                Presentsid:"",
                
                Abscentsid:"",
                
                user_id_2:"",
                
                user_id_3:"",
                
                user_id_4:"",
                
                valideur_1:"",
                
                valideur_2:"",
                
                creat_by:"",
                
                extra_attributes:"",
                
                created_at:"",
                
                updated_at:"",
                
                deleted_at:"",
                                }
            }
        },
        methods: {
            createLine () {
                this.isLoading = true
                this.axios.post('/api/programmationsrondes', this.form).then(response => {
                    this.isLoading = false
                    this.resetForm()
                    this.gridApi.applyServerSideTransaction({
                        add: [
                            response.data
                        ],
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            resetForm () {
                this.form = {
                                            id:"",
                                            libelle:"",
                                            description:"",
                                            date_debut:"",
                                            date_fin:"",
                                            default_heure_debut:"",
                                            default_heure_fin:"",
                                            user_id:"",
                                            statut:"",
                                            type:"",
                                            postesbaladeur:"",
                                            valider1:"",
                                            zone_id:"",
                                            valider2:"",
                                            poste_id:"",
                                            etats:"",
                                            postes:"",
                                            min_pointage:"",
                                            Allclients:"",
                                            AllDatesInRange:"",
                                            Presents:"",
                                            Abscents:"",
                                            Presentsid:"",
                                            Abscentsid:"",
                                            user_id_2:"",
                                            user_id_3:"",
                                            user_id_4:"",
                                            valideur_1:"",
                                            valideur_2:"",
                                            creat_by:"",
                                            extra_attributes:"",
                                            created_at:"",
                                            updated_at:"",
                                            deleted_at:"",
                                    }
            }
        }
    }
</script>
